.calendar-year > span,
.calendar-month > span {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  padding: 0.106667rem;
  width: 0.32rem;
  height: 0.32rem;
}

.calendar-year > span.calendar-header-right-arrow,
.calendar-month > span.calendar-header-right-arrow {
  left: auto;
  right: 0;
}

.vux-prev-icon,
.vux-next-icon {
  position: absolute;
  left: 0;
  top: 0.2rem;
  display: inline-block;
  width: 0.16rem;
  height: 0.16rem;
  border: 0.013333rem solid #04BE02;
  border-radius: 0;
  border-top: none;
  border-right: none;
  transform: rotate(45deg);
  margin-left: 0.2rem;
  line-height: 0.533333rem;
}

.vux-next-icon {
  transform: rotate(-135deg);
  left: auto;
  top: 0.186667rem;
  right: 0.2rem;
}

.is-weekend-highlight td.is-week-list-0,
.is-weekend-highlight td.is-week-list-6,
.is-weekend-highlight td.is-week-0,
.is-weekend-highlight td.is-week-6 {
  color: #E59313;
}

.inline-calendar a {
  text-decoration: none;
  tap-highlight-color: rgba(0, 0, 0, 0);
}

.calendar-year,
.calendar-month {
  position: relative;
}

.calendar-header {
  line-height: 0.533333rem;
  font-size: 1.2em;
  overflow: hidden;
}

.calendar-header > div {
  float: left;
  width: 50%;
  text-align: center;
  overflow: hidden;
}

.calendar-header span:last-of-type {
  float: right;
  vertical-align: bottom;
}

.switch-btn,
.calendar-title {
  display: inline-block;
  border-radius: 0.053333rem;
  line-height: 0.4rem;
}

.switch-btn {
  width: 0.4rem;
  margin: 0.066667rem;
  color: #39b5b8;
  font-family: "SimSun";
}

.calendar-title {
  padding: 0 6%;
  color: #333;
}

.switch-btn:active,
.calendar-title:active,
.calendar-header a.active {
  background-color: #39b5b8;
  color: #fff;
}

.calendar-week {
  overflow: hidden;
}

.calendar-week span {
  float: left;
  width: 14.28%;
  font-size: 1.6em;
  line-height: 0.453333rem;
  text-align: center;
}

.inline-calendar {
  width: 100%;
  background-color: #fff;
  border-radius: 0.026667rem;
  transition: all 0.5s ease;
}

.inline-calendar td.is-today,
.inline-calendar td.is-today.is-disabled {
  color: #04BE02;
}

.calendar-enter,
.calendar-leave-active {
  opacity: 0;
  transform: translate3d(0, -0.133333rem, 0);
}

.calendar:before {
  position: absolute;
  left: 0.4rem;
  top: -0.133333rem;
  content: "";
  border: 0.066667rem solid rgba(0, 0, 0, 0);
  border-bottom-color: #DEDEDE;
}

.calendar:after {
  position: absolute;
  left: 0.4rem;
  top: -0.12rem;
  content: "";
  border: 0.066667rem solid rgba(0, 0, 0, 0);
  border-bottom-color: #fff;
}

.inline-calendar table {
  clear: both;
  width: 100%;
  border-collapse: collapse;
  color: #444444;
}

.inline-calendar td {
  padding: 0.066667rem 0;
  text-align: center;
  vertical-align: middle;
  font-size: 0.213333rem;
  position: relative;
}

.inline-calendar td.week {
  pointer-events: none !important;
  cursor: default !important;
}

.inline-calendar td.is-disabled {
  color: #c0c0c0;
}

.inline-calendar td > span.vux-calendar-each-date {
  position: relative;
  display: inline-block;
  width: 0.346667rem;
  height: 0.346667rem;
  line-height: 0.333333rem;
  border-radius: 50%;
  text-align: center;
  border: 0.013333rem solid transparent;
  box-sizing: border-box;
}

.inline-calendar td.current > span.vux-calendar-each-date {
  background-color: #04BE02;
  color: #fff !important;
}

.inline-calendar th {
  color: #000;
  font-weight: normal;
}

/** same as week-calendar style**/

.vux-calendar-top-tip {
  position: absolute;
  left: -0.133333rem;
  top: 0;
  font-size: 0.266667rem;
  transform: scale(0.5);
  transform-origin: top left;
}

.vux-calendar-dot {
  display: block;
  text-align: center;
  width: 0.066667rem;
  height: 0.066667rem;
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: -0.033333rem;
  background-color: #f74c31;
  border-radius: 50%;
}


